.list__container{
margin: var(--spacing-medium) 0;
padding: 0;
list-style-type: none;
column-count: 3;
}

.list__item a{
    display: inline-flex;
    color: var( --color-black);
    text-decoration: none;
    text-align: center;
    cursor: pointer;
}
.person__photo{
width: 70px;
height: 70px;
object-fit: cover;
object-position: center center;
border-radius: var(--spacing-ultra-small);
}
.person_name{
display: flex;
align-items: center;
margin: 0;
padding: 0 var(--spacing-small);
color: var(--theme-default-header);
text-shadow: var(--text-shadow-blue);
}
.person_comment{
    color: var(--theme-default-header);
}
@media screen and (max-width:850px) {
    .list__container{
      
        column-count: 2;
        }
}